body{
  background: $body-color;
  position: relative;
}

._100vh{
  height: 100vh;
}

.section{
  padding: 100px 0;
}

.section__title {
  position: relative;
  margin-bottom: 150px;
  z-index: 1;
  &::before{
    content: attr(data-content);
    font-family: $primary-font;
    font-size: 100px;
    line-height: 100px;
    color: rgb(8, 8, 8);
    opacity: 0.07;
    position: absolute;
    z-index: -1;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%);
    white-space: nowrap;
    @include mobile{
      font-size: 60px;
      line-height: 60px;
    }
  }
  &::after{
    content: '';
    position: absolute;
    width: 160px; height: 4px;
    background: linear-gradient( 0deg, rgb(62,27,189) 0%, rgb(160,97,255) 100%);
    border-radius: 4px;
    left: 50%; transform: translateX(-50%);
    bottom: -20px;
  }
  &.white{
    color: $white;
    &::before{
      color: $white;
    }
    &::after{
      background: #f5f7f9;
      opacity: .33;
    }
  }
  &.left{
    margin-bottom: 100px;
    @include desktop{
      margin-bottom: 50px;
    }
    @include mobile{
      font-size: 50px;
    }
    &:before, &:after{
      left: 0; transform: translateX(0);
    }
  }
  @include mobile{
    font-size: 50px;
  }
}

.love-animation-one{
  position: absolute;
  z-index: 3;
  height: 80px;
  svg{
    &:first-child{
      animation-name: animate-love;
      animation-duration: 3s;
      animation-delay: -1s;
      animation-iteration-count: infinite;
      position: absolute;
      width: 30px;
      left: 50%; transform: translateX(-50%);
      top: 10%;
    }
    &:nth-child(2){
      animation-name: animate-love;
      animation-duration: 3s;
      animation-delay: -2s;
      animation-iteration-count: infinite;
      position: absolute;
      width: 20px;
      left: 50%; transform: translateX(-50%);
      top: 50%;
    }
    &:last-child{
      animation-name: animate-love;
      animation-duration: 3s;
      animation-delay: -3s;
      animation-iteration-count: infinite;
      position: absolute;
      width: 15px;
      top: 80%;
      left: 50%; transform: translateX(-50%);
    }
  }
}

@keyframes animate-love {
  0%  {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.form-control{
  background: #f6f9fa;
  border: 1px solid #d4d4d4;
  font-family: $primary-font;
  padding: 20px 30px;
}