.login{
  background: linear-gradient( 108deg, rgb(255,142,127) 0%, rgb(255,87,154) 100%);
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
  @include desktop{
    height: auto;
  }
  &__shape{
    &-bottomleft{
      position: absolute;
      width: 40%;
      bottom: 0;
      left: -10%;
      opacity: .52;
      img{
        width: 100%;
      }
    }
    &-topright{
      position: absolute;
      width: 40%;
      right: -5%;
      top: -15%;
      img{
        width: 100%;
      }
    }
    &-small{
      position: absolute;
      width: 80%;
      top: 5%;
      left: 10%;
      @include desktop-lg{
        width: 100%;
      }
      img{
        width: 100%;
      }
    }
  }
  &__card{
    background: $white;
    box-shadow: 0px 45px 60px 0px rgba(25, 28, 33, 0.15);
    text-align: center;
    border-radius: 4px;
    padding: 50px;
    @include mobile{
      padding: 50px 30px;
    }
    @include mobile-xs{
      padding: 30px 20px;
      margin-top: 25px;
    }
    &-wrapper{
      position: relative;
      z-index: 1;
    }
    &-shapeLeft{
      position: absolute;
      z-index: -1;
      left: -45%;
      bottom: 0;
      @include tablet{
        left: -28%;
        width: 250px;
      }
      @include mobile{
        display: none;
      }
    }
    &-shapeRight{
      position: absolute;
      z-index: -1;
      right: -33%;
      bottom: 0;
      @include tablet{
        width: 180px;
        right: -20%;
      }
      @include mobile{
        display: none;
      }
    }
    .logo{
      width: 200px;
      margin-bottom: 30px;
      @include mobile-xs{
        margin-bottom: 10px;
      }
    }
    .title{
      color: #898989;
      span{
        font-family: $primary-font;
        color: #431fc1;
      }
      margin-bottom: 30px;
    }
  }
  &__form{
    &-field{
      position: relative;
      span{
        position: absolute;
        right: 30px;
        top: 50%; transform: translateY(-50%);
        color: #898989;
        cursor: pointer;
      }
    }
    &-submit{
      i{
        transform: translateY(3px);
        margin-left: 10px;
        font-size: 24px;
      }
      border: none;
    }
    input{
      margin-bottom: 20px;
      border-radius: 50px;
      @include mobile-xs{
        margin-bottom: 10px;
      }
    }
  }
}