.about{
  padding: 100px 0;
  position: relative;
  &__shape{
    &-small{
      position: absolute;
      width: 80%;
      height: 30%;
      top: 10%;
      left: 50%; transform: translateX(-50%);
      svg{
        width: 100%;
        height: auto;
      }
    }
    &-circle{
      position: absolute;
      width: 200px;
      right: 15%;
      bottom: -20%;
      z-index: 4;
      @include desktop{
        width: 150px;
        bottom: -7%;
      }
    }
  }
  &__image{
    @include desktop{
      margin-top: 100px;
    }
    position: relative;
    img{
      width: 100%;
    }
    .topLike{
      position: absolute;
      width: 40px;
      left: 0; top: 0;
    }
    .rightLike{
      position: absolute;
      width: 80px;
      top: 30px; right: 0;
    }
    &-love{
      position: absolute;
      width: 75px;
      top: -50px;
      right: 15%;
      svg{
        width: 100%;
        path{
          fill: #dc396a;
        }
      }
    }
  }
}