.hero{
  background: linear-gradient( 108deg, rgb(255,142,127) 0%, rgb(255,87,154) 100%);
  position: relative;
  overflow: hidden;
  padding: 200px 0;
  @include desktop{
    padding: 150px 0;
  }
  &__shape{
    &-bottomleft{
      position: absolute;
      width: 40%;
      bottom: 0;
      left: -10%;
      opacity: .52;
      img{
        width: 100%;
      }
    }
    &-topright{
      position: absolute;
      width: 40%;
      right: -5%;
      top: -15%;
      img{
        width: 100%;
      }
    }
    &-love-1{
      position: absolute;
      width: 75px;
      top: 30%;
      left: 50%;
      @include desktop-lg{
        top: 22%;
        left: 52%;
      }
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
      @include desktop{
        top: 57%; left: 15%;
      }
      @include tablet{
        left: 12%;
        top: 60%;
      }
      @include mobile{
        display: none;
      }
    }
    &-love-2{
      position: absolute;
      width: 75px;
      bottom: 10%;
      left: 5%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-love-3{
      position: absolute;
      width: 75px;
      right: 7%;
      top: 10%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-love-4{
      position: absolute;
      width: 75px;
      bottom: 3%;
      right: 2%;
      svg{
        width: 100%;
        height: auto;
        path{
          fill: $white;
          opacity: .47;
          &.love-small{
            transform-origin: center center;
          }
        }
      }
    }
    &-small{
      position: absolute;
      width: 80%;
      top: 5%;
      left: 10%;
      @include desktop-lg{
        width: 100%;
      }
      img{
        width: 100%;
      }
    }
  }
  &__content{
    @include desktop{
      text-align: center;
      margin-bottom: 100px;
    }
    h1,p,span{
      color: $white;
    }
    h1{
      font-size: 90px;
      @include mobile{
        font-size: 60px;
      }
    }
    p{
      font-size: 17px;
      margin-bottom: 80px;
      @include desktop{
        margin-bottom: 40px;
      }
    }
    span{
      display: block;
      font-size: 15px;
      margin-left: 20px;
      @include desktop{
        margin-left: 0;
      }
    }
    &-btn{
      position: relative;
      width: 75%;
      @include desktop{
        left: 50%;
        transform: translateX(-50%);
      }
      .tips{
        position: absolute;
        background: $white;
        color: $primary-color;
        display: block;
        white-space: nowrap;
        padding: 5px 10px;
        font-size: 12px;
        font-weight: normal;
        font-family: $primary-font;
        text-align: center;
        left: 50%; transform: translateX(-50%);
        bottom: -25px;
        border-radius: 3px;
        transition: .3s ease;
        opacity: .95;
        margin-left: 5px;
        &::before{
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 0 7px 7px 7px;
          border-color: transparent transparent $white transparent;
          top: -7px;
          left: 50%; transform: translateX(-50%);
        }
        @include desktop{
          left: 50%;
        }
      }
    }
    .btn{
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 700;
      letter-spacing: 1px;
      position: relative;
      i{
        display: inline-block;
        font-size: 30px;
        transform: translateY(4px);
        margin-left: 25px;
        background-image: linear-gradient( -53deg, rgb(233,83,122) 0%, rgb(255,150,136) 100%);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      @include mobile{
        font-size: 16px;
        i{
          font-size: 20px;
          margin-left: 15px;
          transform: translateY(1);
        }
      }
      &:hover{
        i{
          -webkit-background-clip: text;
          background-clip: text;
          -webkit-text-fill-color: $white;
        }
        .tips{
          font-size: 13px;
          bottom: -50px;
        }
      }
    }
  }
  &__image{
    position: relative;
    z-index: 1;
    @include desktop{
      text-align: center;
    }
    .middle{
      width: 100%;
      box-shadow: 0px 39px 53px 0px rgba(25, 28, 33, 0.07); 
      @include desktop{
        width: 75%;
      }
      @include tablet{
        width: 90%;
      }
    }
    .leftCharacter{
      position: absolute;
      bottom: -15%; left: -30%;
      width: 250px;
      @include desktop{
        left: -5%;
      }
      @include tablet{
        width: 180px;
        bottom: -10%;
        left: -10%;
      }
      @include mobile-xs{
        display: none;
      }
    }
    .rightCharacter{
      position: absolute;
      right: -25%; bottom: -22%;
      width: 300px;
      @include desktop{
        right: 0;
        bottom: -10%;
      }
      @include tablet{
        width: 200px;
        right: -5%;
      }
      @include mobile{
        right: -10%;
        bottom: -30%;
      }
    }
    .topLike{
      position: absolute;
      top: -50px; left: 20px;
      width: 40px;
      @include desktop{
        left: 20%;
      }
      @include tablet{
        width: 30px;
      }
    }
    .rightLike{
      position: absolute;
      right: -28%; bottom: 21%;
      @include desktop{
        right: -5%;
      }
      @include tablet{
        width: 50px;
      }
      @include mobile{
        right: 0;
      }
    }
    .leftTree{
      position: absolute;
      width: 280px;
      left: -30%;
      top: -20%;
      z-index: -1;
      transform: scaleX(-1);
      opacity: .3;
    }
    .rightTree{
      position: absolute;
      width: 250px;
      right: -20%;
      top: -15%;
      transform: scaleX(-1);
      z-index: -1;
      opacity: .3;
    }
  }
}